html.dark, body.dark {
  background-color: #020D0C !important;
  color: #F27830 !important;
}

.dark .app {
  background-color: #020D0C !important;
}

.dark .card {
  background-color: #590202 !important;
  box-shadow: 0 4px 8px rgba(242, 120, 48, 0.2) !important;
  color: #F27830 !important;
}

.dark .header,
.dark .contact footer {
  background-color: #8C0303 !important;
  color: #F27830 !important;
}

.dark button {
  background-color: #D91414 !important;
  color: #F27830 !important;
}

.dark input,
.dark textarea {
  background-color: #590202 !important;
  border-color: #8C0303 !important;
  color: #F27830 !important;
}

.dark input:focus,
.dark textarea:focus {
  outline: none !important;
  border: 2px solid #D91414 !important;
}

.dark h1,
.dark h2,
.dark h3,
.dark p,
.dark li,
.dark a,
.dark span {
  color: #F27830 !important;
}

.dark .toggle-switch-bg {
  background-color: #590202 !important;
}

.dark .dot {
  background-color: #8E1616 !important;
}

.dark * {
  background-color: transparent !important;
}

.dark *:not(.card, .header, .contact footer, button, input, textarea, .toggle-switch-bg, .dot) {
  background-color: #020D0C !important;
}